
#app {
  /* font-family: 'Avenir', Helvetica, Arial, sans-serif; */
  font-family: cursive;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 50px;
}
* {
  margin: 0;
  padding: 0;
}
html {
  height: 100%;
  /* background: gray; */
}
li {
  list-style: none;
}

.dashboard-container[data-v-4132be0c] {
  padding: 1rem 5rem;
}
.inner-container[data-v-4132be0c] {
  position: relative;
}
.search-and-create[data-v-4132be0c] {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
}

.search input {
    padding: 3px;
}

.bt-createnew {
  /* margin-right: 10%; */
  /* margin-left: 10%; */
  cursor: pointer;
}
.pop-up-bg {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.5);
}
.pop-up {
  position: fixed;
  top: 50%;
  left: 50%;
  padding: 3rem 2rem 1rem;  
  -webkit-transform: translate(-50%,-50%);  
          transform: translate(-50%,-50%);
  background: white;
  /* border: 1px solid #b0ced8; */
  text-align: left;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
.pop-up-item {
  margin-bottom: 2rem;
}
.pop-up label {
  margin-right: 5px;
}
.input-title {
  border: 1px solid gray;
  /* border-style: none; */
}
.bt-container {
  text-align: right;
  overflow: hidden;
}
.bt-container button {
  padding: 8px 10px;
  color: white;
  border: none;
  /* border-style: none; */
}
.bt-cancel {
  background: #2196F3;
}
.bt-submit {
  background: #4caf50;
}

.page-number ul {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}
.page-number {
  position: absolute;
  margin-top: 0.5rem;
  right: 0;
}
.page-number ul li {
  margin-left: 5px;
}
.page-number .bt {
}
.page-number .bt-pre span {
  border-right: 6px solid gray;
}
.page-number .bt-next span{
  border-left: 6px solid gray;
}
.page-number .bt span {
  display: inline-block;
  width: 0;
  height: 0;
  margin: 0 3px;
  border-top: 6px solid transparent;
  border-bottom: 6px solid transparent;
  cursor: pointer;
}
.page-number .bt-pre span:active {
  border-right-color: lightgray;
}
.page-number .bt-next span:active {
  border-left-color: lightgray;
}

.list-container {
  margin-top: 1rem;
  /* min-height: 280px; */
  background: #d8ebf1;
  border: 1px solid #9fb0b7;
}
.list-container ul {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  padding: 2rem 1rem;
}
.list-item {
  margin: 0 0.6rem;
  width: 30%;
}
.list-item-title {
  font-weight: normal;
  margin-bottom: 0.5rem;
  padding: 3px; 
  border-radius: 15px;   
  letter-spacing: 0.5px;
  background: rgba(255, 255, 255, 1);
}
.list-item-img {
  width: 100%;
  border: 1px solid #d0cdb5;
}
